import React, {Component} from 'react';

import particlesConfig from './particles-config.json'

class Hero extends Component {

  constructor(props) {
    super(props)
  }

  componentDidMount() {
    require.ensure(
      '@dschau/particles.js',
      () => {
        this.Particles = require('@dschau/particles.js')
        this.Particles(`particles-js`, particlesConfig)
      },
      '@dschau/particles.js'
    )
  }
  
  /*
  componentWillUnmount() {
    if (this.Particles) {
      this.Particles.destroy()
    }
  }
  */

  render() {
    return (
      <div id="particles-js" className="hero">
        <h1>{this.props.title}</h1>
      </div>
    )
  }
}

export default Hero;




